<template>
  <div class="content">
    <div class="content__header">
      <h1 class="content__title">Общие настройки</h1>
      <button
        @click.prevent="save_config"
        class="content__save-button"
        type="submit"
        form="config-form"
      >
        Сохранить
      </button>
    </div>

    <div class="config">
      <div class="config__content">
        <form
          class="config-form"
          ref="config_form"
          id="config_form"
          method="POST"
          enctype="multipart/form-data"
        >
          <config-form :config="GET_CONFIG_LIST_DATA[0]" />
        </form>
      </div>
      <div class="config__footer">
        <div class="config__footer-inner">
          <div class="config__footer-content"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import ConfigForm from "@/components/forms/ConfigForm.vue";
import { mapGetters } from "vuex";
import axios from "axios";

export default {
  name: "ConfigView",
  components: {
    // HelloWorld,
    ConfigForm,
  },
  computed: {
    ...mapGetters(["GET_CONFIG_LIST_DATA"]),
  },
  methods: {
    save_config() {
      let fd = new FormData(this.$refs.config_form);
      console.log(fd);
      axios({
        url: "/admin/config/front/update_config.php",
        method: "post",
        headers: { "Content-Type": "multipart/form-data" },
        data: fd,
      }).then((response) => {
        console.log(response);
        if (response.data == "UPDATECONFIGSUCCESS") {
          this.$store.commit("SET_ALERT_CONTENT", {
            title: "",
            description: "Настройки обновлены",
            status: "success",
            button_text_1: "ОК",
            button_text_2: "",
            is_open: true,
          });
          this.$store.dispatch("CONFIG_LIST_ACTION");
          document.body.classList.add("hidden");
        } else {
          this.$store.commit("SET_ALERT_CONTENT", {
            title: "",
            description: "Что-то пошло не так. Настройки не сохранены!",
            status: "error",
            button_text_1: "ОК",
            button_text_2: "",
            is_open: true,
          });
        }
      });
    },
  },
};
</script>

<style lang="scss">
.content {
  &__save-button {
    padding: 10px 20px;
    background: $blue;
    border: none;
    border-radius: 5px;
    color: #fff;
    cursor: pointer;
    &:hover {
      background: $green;
    }
  }
}
</style>
